import React, { Dispatch, SetStateAction } from "react";
import "./style.css";

// interface:   Properties //
interface Props {
  currentPage: number;
  setCurrentPage: Dispatch<SetStateAction<number>>;
  totalItems: number; //    
  itemsPerPage: number; //     
}

// component:   //
export default function Pagination(props: Props) {
  const { currentPage, setCurrentPage, totalItems, itemsPerPage } = props;

  //    
  const totalPages = Math.ceil(totalItems / itemsPerPage);

  // event handler:     //
  const onPageClickHandler = (page: number) => {
    setCurrentPage(page);
  };

  // event handler:     //
  const onPreviousClickHandler = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  // event handler:     //
  const onNextClickHandler = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  // render:   //
  return (
    <div id="pagination-wrapper">
      <div className="pagination-change-link-box">
        <div
          className="pagination-change-link-text"
          onClick={onPreviousClickHandler}
        >
          {""}
        </div>
      </div>
      <div className="pagination-divider">{"|"}</div>

      {/*    */}
      {Array.from({ length: totalPages }, (_, index) => index + 1).map((page) =>
        page === currentPage ? (
          <div className="pagination-text-active" key={page}>
            {page}
          </div>
        ) : (
          <div
            className="pagination-text"
            onClick={() => onPageClickHandler(page)}
            key={page}
          >
            {page}
          </div>
        )
      )}

      <div className="pagination-divider">{"|"}</div>
      <div className="pagination-change-link-box">
        <div
          className="pagination-change-link-text"
          onClick={onNextClickHandler}
        >
          {""}
        </div>
      </div>
    </div>
  );
}
